body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 100vh;
}

#root,
#root > * {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.mdc-drawer__content {
  -ms-flex: 1 1;
      flex: 1 1;
  height: calc(100% - 10rem);
}

.mdc-drawer-app-content {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  padding: 1.5rem;
}

/** doc styles */
.app__content {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-width: 100vw;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.app__content > div {
  max-width: 50rem;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
}

.app__content h1:first-of-type {
  font-weight: bold;
  margin-top: 4rem;
  color: var(--mdc-theme-primary);
  font-size: 2.125rem;
  font-weight: 400;
  line-height: 2.5rem;
  margin-bottom: 1.875rem;
}

.app__content h1:first-of-type + p {
  font-size: 1.25rem;
  font-weight: light;
}

.app__content h2 {
  font-weight: bold;
  padding-top: 0;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 2rem;
  margin-bottom: 1.875rem;
  overflow: hidden;
  color: black;
}

.app__content p,
ul {
  line-height: 1.4;
  font-size: 1rem;
}

.mdc-list-item--selected,
.mdc-list-item--activated,
.mdc-drawer .mdc-list-item--activated .mdc-list-item__graphic {
  color: var(--mdc-theme-primary) !important;
}

/* code examples */
.example.render-with-code,
.example.code-only {
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  margin: 1.5rem 0;
  background-color: rgba(0, 0, 0, 0.02);
}

:not(pre) > code[class*='language-'],
pre[class*='language-'] {
  -moz-tab-size: 2;
    -o-tab-size: 2;
       tab-size: 2;
  background-color: transparent;
  border-radius: 0.375rem;
  -webkit-overflow-scrolling: touch;
  font-size: 0.875rem;
}

.app__content > div p code,
.app__content > div table code,
.app__content > div ul code {
  font-family: source-code-pro, Menlo, Consolas, Monaco, Andale Mono,
    Courier New, monospace;
  line-height: 1.5;
  background-color: rgba(0, 0, 0, 0.05);
  padding: 2.4px 8px;
  border-radius: 0.125rem;
}

.mdc-text-field:not(.mdc-text-field--disabled) {
  background-color: transparent;
}

/** Drawer */
.mdc-drawer {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
}

.mdc-drawer__header {
  height: 10rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  text-decoration: none;
  -ms-flex-direction: column;
      flex-direction: column;
  background: var(--mdc-theme-primary);
  color: var(--mdc-theme-on-primary);
  padding-bottom: 1rem;
}

.mdc-drawer .mdc-drawer__title {
  color: inherit;
  margin-bottom: -1rem;
}

.mdc-drawer .mdc-drawer__subtitle {
  color: inherit;
  opacity: 0.7;
}

.app__logo {
  width: 3rem;
  height: 3rem;
  background: white;
  border-radius: 50%;
  color: var(--mdc-theme-secondary);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.app__logo svg {
  width: 2.5rem;
  height: 2.5rem;
}

.mdc-fab {
  text-decoration: none;
}

.github-btn {
  margin-bottom: 3rem;
}

@media (min-width: 769px) {
  .app__content {
    max-width: calc(100vw - 16rem);
  }

  .app__content h1:first-of-type {
    margin-top: 1.5rem;
  }

  .mdc-top-app-bar {
    display: none;
  }
}



